    cursor: pointer;
}
.trportpopup{
    border-top:none !important
}

.input-white-bg {
  background-color: #ffffff !important;
  box-shadow: 0 0 0 1000px #ffffff inset !important;
  -webkit-box-shadow: 0 0 0 1000px #ffffff inset !important;
  -webkit-text-fill-color: #000000 !important;
}

/* ===== MUI TextField white background + autofill fix ===== */
.input-white-bg .MuiInputBase-root {
  background-color: #ffffff !important;
}

.input-white-bg input {
  background-color: #ffffff !important;
  color: #000000 !important;
  -webkit-text-fill-color: #000000 !important;
}

/* Chrome autofill fix */
.input-white-bg input:-webkit-autofill,
.input-white-bg input:-webkit-autofill:hover,
.input-white-bg input:-webkit-autofill:focus,
.input-white-bg input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 1000px #ffffff inset !important;
  box-shadow: 0 0 0 1000px #ffffff inset !important;
  -webkit-text-fill-color: #000000 !important;
}
